
* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
html {
  font-size: 10px;
}
body {
  padding: 0;
  margin: 0;
  font-family: "Open Sans", sans-serif;
}
#app {
  width: 100%;
  height: 100%;
  background-color: #444444;
}

.header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  padding: 2.5rem 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: white;
  z-index: 1999;
}
.header__logo img {
    position: absolute;
    top: -13px;
    width: 14rem;
    height: 9rem;
}
.header__navigation {
    font-size: 1.5rem;
}
.header__navigation .list {
      list-style-type: none;
      text-transform: capitalize;
}
.header__navigation .list__item {
        display: inline-block;
}
.header__navigation .list__item:not(:last-child) {
          margin: 0 .8rem;
}

.section-under {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 80px;
  padding: 0 2.2rem;
  color: #fff;
}
.section-under button {
    background-color: yellow;
    border: 0 transparent;
    border-radius: 3px;
    padding: 1rem 2.2rem;
}

.phone-number {
  position: absolute;
  right: 0;
  z-index: 999;
  background: yellow;
  padding: .8rem 3rem;
  font-size: 1.8rem;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 25% 100%);
          clip-path: polygon(0 0, 100% 0, 100% 100%, 25% 100%);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.phone-number img {
    padding: 0 1rem 0 2rem;
}
.phone-number span {
    margin-top: 3px;
}
.slider {
  width: 100%;
  height: 45vh;
}
.slider__item {
    width: 100%;
    height: 45vh;
}
.slider__item .title {
      position: absolute;
      top: 25%;
      z-index: 999;
      background-color: rgba(255, 255, 255, 0.7);
      padding: 1rem 2rem;
      font-size: 1.5rem;
      font-weight: 600;
}
.slider__item .img {
      max-height: 100%;
      width: 100%;
}
.slider .slick-list {
    max-height: 100%;
}
.slider .slick-track {
    max-height: 30vh;
}
.slider .slick-arrow {
    position: absolute;
    top: 50%;
    z-index: 999;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background-color: transparent;
    border: none;
}
.slider .slick-prev {
    left: 2rem;
}
.slider .slick-next {
    right: 2rem;
}
.slider .slick-dots {
    position: absolute;
    left: 50%;
    bottom: 1rem;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    list-style-type: none;
}
.slider .slick-dots li {
      display: inline-block;
      margin: 0 .5rem;
      width: 15px;
      height: 15px;
}
.slider .slick-dots li button {
        width: 100%;
        height: 100%;
        font-size: 0;
        border: transparent;
        border-radius: 50%;
        background-color: rgba(255, 255, 255, 0.75);
        outline: none;
        cursor: pointer;
}
.slider .slick-dots li button:hover {
          background-color: white;
}

.our-offert {
  background-color: yellow;
  width: 100%;
  height: 500px;
}
.our-offert__title {
    font-size: 3rem;
    font-weight: 400;
    text-align: center;
    padding: 2.5rem 0;
}
.our-offert .offert-box {
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    height: 78%;
    margin: 3rem 0;
}
.our-offert .offert-box__item {
      display: inline-block;
      vertical-align: top;
      width: calc(20% - 4rem);
      margin: 0 2rem;
      height: 100%;
}
.our-offert .offert-box__item .item-img {
        width: 100%;
        height: 20rem;
        max-height: 20rem;
}
.our-offert .offert-box .item-description h2 {
      text-align: center;
}
.our-offert .offert-box .item-description p {
      text-align: justify;
      white-space: normal;
}

.example-offert {
  width: 100%;
  margin: 0 auto;
  background-color: #fff;
  padding: 2rem 0;
}
.example-offert .section {
    width: 80%;
    margin: 0 auto;
}
.example-offert .section__img {
      width: 25%;
      display: inline-block;
}
.example-offert .section__description {
      display: inline-block;
      margin-top: 2rem;
      width: 65%;
}
.example-offert .section__description .title {
        font-size: 3rem;
        text-transform: uppercase;
        margin: 1rem 0;
}
.example-offert .section__description .description {
        font-size: 1.8rem;
        letter-spacing: 2px;
}
.example-offert .section__description--top {
        float: right;
}
.example-offert .section__description--bottom {
        float: left;
}

.bottom-section {
  height: 400px;
}
.content, .spacer {
  height: 50%;
}
.content {
  position: relative;
}
.content__title {
    position: absolute;
    right: 5%;
    top: 45%;
    color: #fff;
    font-size: 1.8rem;
}
.content__img {
    height: 100%;
    width: auto;
    margin-left: 5%;
    position: absolute;
    top: 30%;
}
.spacer {
  background-color: yellow;
}



















